import React , {useEffect, useState} from "react";
import { BrowserView, MobileView } from "react-device-detect";
import * as d3 from "d3";
import Streamgraph from "./Streamgraph.js";
import './App.css';

import dataPath from "./all_assets/Uni_Coll_1996_2020.csv";
import { filter } from "d3";

function importAll(r) {
  let images = {};
  r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); });
  return images;
}

const collegeLogos = importAll(require.context('./all_assets/college_twitter_logos', false, /\.(png|jpe?g|svg|jfif)$/));
const universityLogos = importAll(require.context('./all_assets/university_twitter_logos', false, /\.(png|jpe?g|svg|jfif)$/));

function App() {

  const [allData,setAllData] = useState([]) // this is ALL of the data. Not necessarilly the input to the streamgraph
  const [inputData,setInputData] = useState([]) //this is the input to the streamgraph
  const [inputStat,setInputStat] = useState("Count") //this is the input stat to the streamgraph
  const [startYear,setStartYear] = useState(1996) //this is the start year to the streamgraph
  const [endYear,setEndYear] = useState(2020) //this is the end year to the streamgraph

  useEffect(()=>{
    d3.csv(dataPath).then(function(parsedData){
      setAllData(parsedData)
      setInputData(parsedData)

    })
  },[])

  function imgClicker(divID){
    var element = document.getElementById(divID);
    if (element){
      if (element.classList.contains("deselect")){
        // console.log("ACTIVATED!!")
        element.classList.toggle("deselect")
        var selectedData = allData.filter((eachRecord)=>{return eachRecord['Employer']==divID})
        var newInputData = inputData.concat(selectedData)
        setInputData(newInputData)
        // if (element.classList.contains("university-logos")){
          // var uniDiv = document.getElementsByClassName
          // var allInactiveUnis = document.getElementsByClassName("deselect");
        // }
      }
      else{
        // console.log("DEACTIVATED!")
        element.classList.toggle("deselect")
        var newInputData = inputData.filter((eachRecord)=>{return eachRecord['Employer']!=divID})
        setInputData(newInputData)
      }
    // var allInactive = document.getElementsByClassName("inactive");
    // console.log("inAcive",allInactive)
    // var inactiveUni = allInactive.getElementsByClassName("university-logo");
    // var inactiveColl = allInactive.getElementsByClassName("college-logo");
    // if (inactiveColl.length > 5){console.log("coll>5")};
    // if (inactiveUni.length > 6){console.log("uni>5")}
  }}

  function allClicker(employerSector,selectType,sectorClassName){
    var allElements = document.getElementsByClassName(sectorClassName);
    for (var elementIndex = 0; elementIndex < allElements.length; elementIndex++){
      // console.log(allElements[elementIndex])
      if (selectType == "selectAll"){
          allElements[elementIndex].classList.remove("deselect")
          var chosenData = allData.filter((eachRecord)=>{return eachRecord['Sector']==employerSector})
          var removeIfData = inputData.filter((eachRecord)=>{return eachRecord['Sector']!=employerSector})
          var newInputData = chosenData.concat(removeIfData)
          setInputData(newInputData)
      }
      else{
        allElements[elementIndex].classList.add("deselect")
        var newInputData = inputData.filter((eachRecord)=>{return eachRecord['Sector']!=employerSector})
        // console.log("deselectAll",newInputData)
        setInputData(newInputData)
      }
    }
  }

  function toggleShow(sectorDivClassName){
    var allElements = document.getElementsByClassName(sectorDivClassName);
    // console.log(allElements)
    if (allElements[0]){
      allElements[0].classList.toggle("display")
    }
  }

  function checkIfAll(theClassName){
    // console.log(theClassName)
  }

  return (
    <React.Fragment>

      <BrowserView viewClassName = "app browserView">
        <div className = "window-container browser">
          <div className= "header browser">
            <h3>25 years of Sunshine</h3>
            <hr className = "separator"></hr>
            <p> A Streamgraph Visualization of Sunshiners in Ontario's Universities and Colleges</p>
          </div>
          <div className = "stat-selector browser">
            <button className = "stat-button browser" onClick = {function(e){return setInputStat("Count")}}>Number of Employees</button>
            <button className = "stat-button browser" onClick = {function(e){return setInputStat("Total")}}>Total Income</button>
            <button className = "stat-button browser" onClick = {function(e){return setInputStat("Average")}}>Per-Capita Income</button>
          </div>
          <Streamgraph inputData = {inputData} stat = {inputStat}/>
        </div>
        <div className = "controls-container browser">
          <div className = "university-logos browser">
            <div className = "logos-header browser">
              <button 
              className = "logos-title browser selected"
              onClick = {function(){
                var thisButton = document.querySelector(".university-logos").querySelector(".logos-title");
                if(thisButton.classList.contains("selected"))
                {
                  thisButton.classList.toggle("selected")
                  allClicker("Universities","deSelectAll","university-logo")
                }
                else{
                  thisButton.classList.toggle("selected")
                  allClicker("Universities","selectAll","university-logo")
                }
              }}
              >Universities</button>
            </div>
            <div className = "bunch-of-logos university-logos browser">
              {Object.keys(universityLogos).map(eachLogo=>{
                return(
                  <div 
                  className = 'one-logo university-logo browser' 
                  key = {eachLogo} 
                  id = {eachLogo.toString().split(".").slice(0,1)}
                  onClick = {function(){
                    var divID = eachLogo.toString().split(".").slice(0,1)
                    imgClicker(divID);
                    // console.log("hahah")
                  }}
                  >
                <img src = {universityLogos[eachLogo]['default']}/>
                </div>
              )
            })}
            </div>
          </div>
          <div className = "college-logos browser">
            <div className = "logos-header browser">
              <button 
              className = "logos-title browser selected"
              onClick = {function(){
                var thisButton = document.querySelector(".college-logos").querySelector(".logos-title");
                if(thisButton.classList.contains("selected"))
                {
                  thisButton.classList.toggle("selected")
                  allClicker("Colleges","deSelectAll","college-logo")
                }
                else{
                  thisButton.classList.toggle("selected")
                  allClicker("Colleges","selectAll","college-logo")
                }
              }}
              >Colleges</button>
            </div>
            <div className = "bunch-of-logos college-logos browser">
              {Object.keys(collegeLogos).map(eachLogo=>{
                return(
                  <div 
                  className = 'one-logo college-logo browser' 
                key = {eachLogo} 
                id = {eachLogo.toString().split(".").slice(0,1)}
                onClick = {function(){
                  var divID = eachLogo.toString().split(".").slice(0,1)
                  imgClicker(divID)
                }}
                >
                <img src = {collegeLogos[eachLogo]['default']}/>
                </div>
              )
            })}
            </div>
          </div>
        </div>
      </BrowserView>
      
      <MobileView viewClassName = "app mobileView">
        <div className = "header mobile">
          <h4>25 Years of Sunshine</h4>
          <p> A Streamgraph visualization of Sunshiners in Ontario's Universities and Colleges </p>
        </div>
        <div className = "controls-container mobile">
            <div className = "university-logos mobile">
              <div className = "logos-header mobile">

                <p className = "logos-title mobile">Universities</p>

                <button 
                  className = "logos-all mobile" 
                  onClick = {function(){allClicker("Universities","selectAll","university-logo")}}
                >
                  SELECT ALL</button>
                <button 
                className = "logos-all mobile"
                onClick = {function(){allClicker("Universities","deSelectAll","university-logo")}}
                >DESELECT ALL</button>
                
                <button
                  className = "toggle-show mobile"
                  onClick = {function(){toggleShow("bunch-of-logos university-logos")}}
                  >HIDE/SHOW
                </button>
              </div>

              <div className = "bunch-of-logos university-logos mobile">
                {Object.keys(universityLogos).map(eachLogo=>{
                  return(
                    <div 
                    className = 'one-logo university-logo mobile' 
                    key = {eachLogo} 
                    id = {eachLogo.toString().split(".").slice(0,1)}
                    onClick = {function(){
                      var divID = eachLogo.toString().split(".").slice(0,1)
                      imgClicker(divID)
                    }}
                    >
                  <img src = {universityLogos[eachLogo]['default']}/>
                  </div>
                )
              })}
              </div>
            </div>
            <div className = "college-logos mobile">
              <div className = "logos-header mobile">
                <p className = "logos-title mobile">Colleges</p>
                
                <button 
                  className = "logos-all mobile"
                  onClick = {function(){allClicker("Colleges","selectAll","college-logo")}}
                  >Select All
                </button>
                
                <button 
                  className = "logos-all mobile"
                  onClick = {function(){allClicker("Colleges","deSelectAll","college-logo")}}
                  >Deselect All
                </button>
                
                <button
                  className = "toggle-show mobile"
                  onClick = {function(){toggleShow("bunch-of-logos college-logos")}}
                  >HIDE/SHOW
                </button>

              </div>
              <div className = "bunch-of-logos college-logos mobile">
                {Object.keys(collegeLogos).map(eachLogo=>{
                  return(
                    <div 
                    className = 'one-logo college-logo mobile' 
                  key = {eachLogo} 
                  id = {eachLogo.toString().split(".").slice(0,1)}
                  onClick = {function(){
                    var divID = eachLogo.toString().split(".").slice(0,1)
                    imgClicker(divID)
                  }}
                  >
                  <img src = {collegeLogos[eachLogo]['default']}/>
                  </div>
                )
              })}
              </div>
            </div>
          </div>
          <div className = "stat-selector mobile">
              <button className = "stat-button mobile" onClick = {function(e){return setInputStat("Count")}}>Number of Employees</button>
              <button className = "stat-button mobile" onClick = {function(e){return setInputStat("Total")}}>Total Income</button>
              <button className = "stat-button mobile" onClick = {function(e){return setInputStat("Average")}}>Per-Capita Income</button>
              {/* <select  onChange = {function(e){console.log(e.target.value);return setInputStat(e.target.value)}}>
                <option value = {"Count"}>{"Number of Employees"}</option>
                <option value = {"Total"}>{"Total Income of Employees"}</option>
              </select> */}
            </div>
          <Streamgraph inputData = {inputData} stat = {inputStat}/>
      </MobileView>

    </React.Fragment>
  );
}

export default App;
